import React, { useEffect } from 'react'
import { Prompt } from 'react-router-dom'

// TODO: Prompt is fairly janky (e.g. changes URL anyway: https://github.com/remix-run/react-router/issues/5405); use React Router v6 when this behaviour supported

export const ConfirmNavigateAway = ({ message }: { message: string }): JSX.Element => {
  useEffect(() => {
    const listener = (e: BeforeUnloadEvent) => {
      e.preventDefault()
      e.returnValue = ''
    }
    window.addEventListener('beforeunload', listener)
    return () => {
      window.removeEventListener('beforeunload', listener)
    }
  }, [])

  return <Prompt when={true} message={message} />
}
